.gh-header {
    margin-bottom: 4vmin;
    text-align: center;
}

.gh-header > * {
    grid-column: wide-start / wide-end;
}

.gh-header h1 {
    max-width: 20em;
    margin-left: auto;
    margin-right: auto;
}

.gh-feature-image {
    width: 100%;
    max-width: 800px;
    margin: 30px auto 0;
}

@media (max-width: 800px) {
    .gh-feature-image {
        grid-column: full-start / full-end;
    }
}

.gh-post-meta {
    display: block;
    margin: 0.5rem 0 0;
    font-size: 1.3rem;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--color-grey);
    font-weight: 500;
}